.simplebar-track.simplebar-vertical{width:2px;background-color:hsla(0,0%,100%,.25);border-radius:1px;overflow:visible}.simplebar-vertical .simplebar-scrollbar{width:4px;right:0;left:-1px}.simplebar-vertical .simplebar-scrollbar:before{width:4px;right:0;left:0;background:#fff;border-radius:2px}.simplebar-vertical .simplebar-scrollbar.simplebar-visible:before{opacity:1;top:0;bottom:0}